.skills-posed {

  background-color: #eeeeee;
  height: 100vh;

  .cells-container-skills {
    .cell-skill {
      margin: 2vw 0;
      padding: 1vw 4vw;
      background-color: white;

      .cell-skill-main {
        display: flex;
        flex-direction: row;
        justify-content: space-between;

        .bei_zhu {
          min-width: 50vw;
          font-size: 1.3em;
          font-weight: 500;
        }

        .skill-img {
          padding: 2vw;
          width: 30vw;
          max-height: 30vw;
        }
      }

      .cell-skill-footer{
        //text-align: right;
        padding: 1vw;
        .update-skill{
          color: #2d8cdd;
          padding: 1vw;
        }
        .tpl-tag{
          background-color: #71ceff;
          color: white;
          border-radius: 5px;
          padding: 1vw 2vw;
          //float: left;
        }
        .c-time{
          float: right;
        }

      }

    }
  }
}

.my-favs {
  .fav-cell {
    list-style: none;
    border-radius: 10px;
    margin: 2vw;
    padding: 5vw 5vw;
    background-color: white;
    box-shadow: 0 5px 10px 0 rgba(174, 174, 174, 0.4);

    img {
      width: 100%;
    }

    span {
      font-size: 20px;
      font-weight: bold;
      padding: 3px 10px;
      border-radius: 50px;
      color: rgba(201, 160, 47, 0.9);
      background-color: rgba(255, 231, 166, 0.9);
    }
  }
}
